import React, { useEffect, useState } from "react";
import HeaderTopCommon from "../../components/common/HeaderTop";
import { WrapperContainer } from "../../style-App";
import { useDispatch } from "react-redux";
import HorizontalSidebar from "../../components/horizontal-sidebar";
import VerticalSidebar from "../../components/vertical-sidebar";
import CategoryComponents from "../../components/category";
import { CategoriesGet, SubCategoriesGet } from "../../redux/category";

function Category() {
  const [open, setOpen] = useState(false);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(CategoriesGet());
    dispatch(SubCategoriesGet());
  }, []);

  const handleClose = () => setOpen(false);
  const handleOpen = () => setOpen(true);

  return window.localStorage.getItem("checked") == "false" ? (
    <HorizontalSidebar>
      <WrapperContainer style={{ marginTop: "112px" }}>
        <HeaderTopCommon
          title={"Категории"}
          onClick={handleOpen}
          textBtn={"Добавить категорию"}
        />
        <CategoryComponents handleClose={handleClose} open={open} />
      </WrapperContainer>
    </HorizontalSidebar>
  ) : (
    <VerticalSidebar>
      <WrapperContainer>
        <HeaderTopCommon
          title={"Категории"}
          onClick={handleOpen}
          textBtn={"Добавить категорию"}
        />
        <CategoryComponents handleClose={handleClose} open={open} />
      </WrapperContainer>
    </VerticalSidebar>
  );
}
export default Category;
