import React, { useState } from "react";
import GetCategories from "./get";
import PostCategory from "./post";
import { useDispatch } from "react-redux";
import {
  CategoriesGet,
  CategoryDelete,
  SubCategoryDelete,
} from "../../redux/category";
import PutCategory from "./put";
import SubPostCategory from "./subpost";
import SubPutCategory from "./subput";

function CategoryComponents({ open, handleClose }) {
  const dispatch = useDispatch();
  const [adminId, setAdminId] = useState();
  const HandelDelete = async (e) => {
    await dispatch(CategoryDelete(e.currentTarget.id));
    dispatch(CategoriesGet());
  };
  const [openPut, setOpenPut] = useState(false);

  const onClosePut = () => {
    setOpenPut(false);
  };

  const handlePutModal = async (e) => {
    setOpenPut(true);
    setAdminId(e.currentTarget.id);
  };
  const [subId, setSubId] = useState();

  const [SubopenPut, setSubOpenPut] = useState(false);

  const onClosePutSub = () => {
    setSubOpenPut(false);
  };

  const handlePutModalSub = async (e) => {
    setSubOpenPut(true);
    setSubId(e.currentTarget.id);
  };

  const HandelDeleteSub = async (e) => {
    console.log(e.currentTarget.id);
    await dispatch(SubCategoryDelete(e.currentTarget.id));
    dispatch(CategoriesGet());
  };
  const [SubopenPutt, setSubOpenPutt] = useState(false);
  const [idd, setIdd] = useState();

  const onClosePutSubb = () => {
    setSubOpenPutt(false);
  };

  const HandelSubPut = (e) => {
    setSubOpenPutt(true);
    setSubId(e.currentTarget.id);
    setIdd(e.currentTarget.lang);
  };

  return (
    <>
      <GetCategories
        HandelDelete={HandelDelete}
        handlePutModal={handlePutModal}
        handlePutModalSub={handlePutModalSub}
        HandelDeleteSub={HandelDeleteSub}
        HandelSubPut={HandelSubPut}
      />
      <PostCategory Open={open} HandleClose={handleClose} />
      <PutCategory Open={openPut} HandleClose={onClosePut} put_id={adminId} />
      <SubPostCategory
        Open={SubopenPut}
        HandleClose={onClosePutSub}
        cateId={subId}
      />
      <SubPutCategory
        Open={SubopenPutt}
        HandleClose={onClosePutSubb}
        cateId={subId}
      />
    </>
  );
}

export default CategoryComponents;
