import React, { useRef, useState } from "react";
import CommonBtn from "../../common/CommonBtn";
import DrawerCommon from "../../common/Drawer";
import SelectCommon from "../../common/select/index";
import { useDispatch } from "react-redux";
import { Wrapper } from "./styled-index";
import { Row, Col } from "react-grid-system";
import {
  EyeTwoTone,
  EyeInvisibleOutlined,
  LoadingOutlined,
} from "@ant-design/icons";
import { Image, Input, Spin } from "antd";
import InputCommon from "../../common/input";
import { useSelector } from "react-redux";
import "./styles.css";
import { CategoriesGet, SubCategoryPut } from "../../../redux/category";

function SubPutCategory({ Open, HandleClose, cateId, idcate }) {
  const id = cateId;
  const dispatch = useDispatch();
  const idcat = idcate;
  const [nameRu, setNameRu] = useState();
  const [nameEn, setNameEn] = useState();
  const [nameUz, setNameUz] = useState();

  const DataSub = useSelector((state) => state.category.subCategoriesGet.data);

  const HandleSubmit = async (e) => {
    e.preventDefault();
    const body = {
      title_uz: nameUz,
      title_en: nameEn,
      title_ru: nameRu,
      category: idcat,
    };
    await dispatch(SubCategoryPut({ id: id, body }));
    dispatch(CategoriesGet());
    HandleClose();
  };

  const antIcon = (
    <LoadingOutlined
      style={{
        fontSize: 24,
        color: "#000",
      }}
      spin
    />
  );
  return (
    <DrawerCommon
      title={"Изменить подкатегорию"}
      open={Open}
      onClose={HandleClose}
    >
      <>
        <Wrapper onSubmit={HandleSubmit}>
          <div className="input_wrap">
            <h2>Название подкатегории</h2>
            {DataSub.map((elem) =>
              elem.id == id ? (
                <>
                  <Row className="row">
                    <Col className="col" lg={6}>
                      <h4>На русском</h4>
                      <InputCommon
                        defaultValue={elem.title_ru}
                        onChange={(e) => setNameRu(e.currentTarget.value)}
                      />
                    </Col>
                    <Col className="col" lg={6}>
                      <h4>На английском</h4>
                      <InputCommon
                        defaultValue={elem.title_en}
                        onChange={(e) => setNameEn(e.currentTarget.value)}
                      />
                    </Col>
                    <Col className="col" lg={6}>
                      <h4>На узбекском</h4>
                      <InputCommon
                        defaultValue={elem.title_uz}
                        onChange={(e) => setNameUz(e.currentTarget.value)}
                      />
                    </Col>
                  </Row>
                </>
              ) : null
            )}
          </div>

          <CommonBtn
            type={"submit"}
            style={{
              padding: "12px 40px",
              border: "2px solid #fff",
            }}
          >
            Изменить
          </CommonBtn>
        </Wrapper>
      </>
    </DrawerCommon>
  );
}

export default SubPutCategory;
