import React, { useRef, useState } from "react";
import CommonBtn from "../../common/CommonBtn";
import DrawerCommon from "../../common/Drawer";
import SelectCommon from "../../common/select/index";
import { useDispatch } from "react-redux";
import { Wrapper } from "./styled-index";
import { Row, Col } from "react-grid-system";
import {
  EyeTwoTone,
  EyeInvisibleOutlined,
  LoadingOutlined,
} from "@ant-design/icons";
import { Image, Input, Spin } from "antd";
import InputCommon from "../../common/input";
import { useSelector } from "react-redux";
import "./styles.css";
import {
  CategoriesGet,
  CategoryPost,
  CategoryUpload,
} from "../../../redux/category";

function PostCategory({ Open, HandleClose }) {
  const dispatch = useDispatch();
  const [nameRu, setNameRu] = useState();
  const [nameEn, setNameEn] = useState();
  const [nameUz, setNameUz] = useState();
  const [country, setCountry] = useState();

  const HandleChange = (e) => {
    dispatch(CategoryUpload(e));
  };

  const uploadDatas = useSelector((state) => state.category.categoryUpload);

  const HandleSubmit = async (e) => {
    e.preventDefault();
    const body = {
      category_uz: nameUz,
      category_en: nameEn,
      category_ru: nameRu,
      image: uploadDatas.data,
    };
    await dispatch(CategoryPost(body));
    window.location.reload();
  };

  const antIcon = (
    <LoadingOutlined
      style={{
        fontSize: 24,
        color: "#000",
      }}
      spin
    />
  );
  return (
    <DrawerCommon
      title={"Добавить категорию"}
      open={Open}
      onClose={HandleClose}
    >
      <>
        <Wrapper onSubmit={HandleSubmit}>
          <div>
            <div className="input_wrap">
              <Row className="row">
                <Col className="col" lg={4}>
                  <Row className="row">
                    <Col className="col_upload" lg={6}>
                      {uploadDatas.Loading == true ? (
                        <div className="spinss">
                          <Spin indicator={antIcon} />
                        </div>
                      ) : uploadDatas.Success == true ? (
                        <Image
                          style={{
                            aspectRatio: "1 / 1",
                            borderRadius: "20px",
                            zIndex: "99999999",
                            objectFit: "cover",
                          }}
                          src={uploadDatas.data}
                        />
                      ) : (
                        <div className="none_img">
                          <i class="bx bxs-image"></i>
                        </div>
                      )}
                    </Col>
                    <Col className="col_upload" lg={6}>
                      {uploadDatas.Loading == true ? (
                        <div className="spins">
                          <Spin indicator={antIcon} />
                        </div>
                      ) : (
                        <>
                          <input
                            type="file"
                            id="file"
                            onChange={HandleChange}
                          />
                          <label for="file" class="custom-file-upload">
                            <span className="span-download">
                              <ion-icon name="cloud-download-outline"></ion-icon>
                              <span>Загрузить фото</span>
                            </span>
                          </label>
                        </>
                      )}
                    </Col>
                    <Col lg={12}>
                      <div className="infor_box">
                        <p>
                          <span>Формат: </span>PNG, JPEG, JPG, SVG.
                          Рекомендуемое разрешение <span>1080×1080</span>
                        </p>
                        <p>
                          {" "}
                          <span>Размер: </span>размер файла не должен превышать
                          5 MB
                        </p>
                      </div>
                    </Col>
                  </Row>
                </Col>
                <Col className="col" lg={8}>
                  <h2>Название категори</h2>
                  <Row className="row">
                    <Col className="col" lg={6}>
                      <h4>На русском</h4>
                      <InputCommon
                        onChange={(e) => setNameRu(e.currentTarget.value)}
                      />
                    </Col>
                    <Col className="col" lg={6}>
                      <h4>На английском</h4>
                      <InputCommon
                        onChange={(e) => setNameEn(e.currentTarget.value)}
                      />
                    </Col>
                    <Col className="col" lg={6}>
                      <h4>На узбекском</h4>
                      <InputCommon
                        onChange={(e) => setNameUz(e.currentTarget.value)}
                      />
                    </Col>
                  </Row>
                </Col>
              </Row>
            </div>
          </div>
          <CommonBtn
            type={"submit"}
            style={{
              padding: "12px 40px",
              border: "2px solid #fff",
            }}
          >
            Добавить
          </CommonBtn>
        </Wrapper>
      </>
    </DrawerCommon>
  );
}

export default PostCategory;
