import React from "react";
import styles from "./style.module.css";
import { Col, Row } from "react-grid-system";
import { useSelector } from "react-redux";
import { Image, Popover, Space } from "antd";

function GetCategories({
  HandelDelete,
  handlePutModal,
  handlePutModalSub,
  HandelDeleteSub,
  HandelSubPut,
}) {
  const CategoriesData = useSelector(
    (state) => state.category.categoriesGet.data
  );
  return (
    <>
      <div className={styles.wrapper}>
        <Row style={{ margin: 0 }}>
          {CategoriesData.map((elem) => (
            <Col lg={4}>
              <div className={styles.box}>
                <div className={styles.img}>
                  <Image
                    style={{
                      aspectRatio: "1 / 1",
                      borderRadius: "20px",
                      zIndex: "99999999",
                      objectFit: "cover",
                    }}
                    src={elem.image}
                  />
                </div>
                <div className={styles.head}>
                  <h3>{elem.category_ru}</h3>
                  <div className={styles.actions}>
                    <Space wrap>
                      <Popover
                        trigger="click"
                        placement="rightBottom"
                        content={
                          <div className="content_delete_box">
                            <p>Вы уверены, что хотите удалить эту категорию?</p>
                            <div className="btn_wrap_delete">
                              <button
                                id={elem.id}
                                onClick={HandelDelete}
                                className={styles.yes_btn}
                              >
                                Удалить
                              </button>
                            </div>
                          </div>
                        }
                        title={
                          <div className="delete_box">
                            <i class="bx bxs-error-circle"></i>
                            <span>
                              Удалите категорию <span>{elem.category_ru}</span>
                            </span>
                          </div>
                        }
                      >
                        <div className="btn-wrap">
                          <button id={elem.id}>
                            <i class="bx bxs-trash"></i>
                          </button>
                        </div>
                      </Popover>
                    </Space>
                    <button onClick={handlePutModal} id={elem.id}>
                      <i id={elem.id} class="bx bx-message-square-edit"></i>
                    </button>
                    <button onClick={handlePutModalSub} id={elem.id}>
                      <i id={elem.id} class="bx bx-plus"></i>
                    </button>
                  </div>
                </div>
                <div className={styles.subList}>
                  <ul>
                    {elem.sub_category.map((ele) => (
                      <li>
                        <h3>{ele.title_ru}</h3>
                        <div className={styles.sub_actions}>
                          <Space wrap>
                            <Popover
                              trigger="click"
                              placement="rightBottom"
                              content={
                                <div className="content_delete_box">
                                  <p>
                                    Вы уверены, что хотите удалить эту
                                    подкатегорию?
                                  </p>
                                  <div className="btn_wrap_delete">
                                    <button
                                      id={ele.id}
                                      className={styles.yes_btn}
                                      onClick={HandelDeleteSub}
                                    >
                                      Удалить
                                    </button>
                                  </div>
                                </div>
                              }
                              title={
                                <div className="delete_box">
                                  <i class="bx bxs-error-circle"></i>
                                  <span>
                                    Удалите подкатегория
                                    <span> {ele.title_ru}</span>
                                  </span>
                                </div>
                              }
                            >
                              <div className="btn-wrap">
                                <button id={elem.id}>
                                  <i class="bx bxs-trash"></i>
                                </button>
                              </div>
                            </Popover>
                          </Space>
                          <button
                            id={ele.id}
                            lang={elem.id}
                            onClick={HandelSubPut}
                          >
                            <i
                              id={ele.id}
                              lang={elem.id}
                              class="bx bx-message-square-edit"
                            ></i>
                          </button>
                        </div>
                      </li>
                    ))}
                  </ul>
                </div>
              </div>
            </Col>
          ))}
        </Row>
      </div>
    </>
  );
}

export default GetCategories;
