import React, { useRef, useState } from "react";
import CommonBtn from "../../common/CommonBtn";
import DrawerCommon from "../../common/Drawer";
import SelectCommon from "../../common/select/index";
import { useDispatch } from "react-redux";
import { Wrapper } from "./styled-index";
import { Row, Col } from "react-grid-system";
import {
  EyeTwoTone,
  EyeInvisibleOutlined,
  LoadingOutlined,
} from "@ant-design/icons";
import { Image, Input, Spin } from "antd";
import InputCommon from "../../common/input";
import { useSelector } from "react-redux";
import "./styles.css";
import {
  CategoriesGet,
  CategoryPost,
  CategoryPut,
  CategoryUpload,
} from "../../../redux/category";

function PutCategory({ Open, HandleClose, put_id }) {
  const id = put_id;
  const dispatch = useDispatch();
  const [nameRu, setNameRu] = useState();
  const [nameEn, setNameEn] = useState();
  const [nameUz, setNameUz] = useState();

  const HandleChange = (e) => {
    dispatch(CategoryUpload(e));
  };

  const DataCategory = useSelector(
    (state) => state.category.categoriesGet.data
  );

  const FilterData = DataCategory.filter((elem) => elem.id == id);

  const uploadDatas = useSelector((state) => state.category.categoryUpload);

  const HandleSubmit = async (e) => {
    e.preventDefault();
    const body = {
      category_uz: nameUz,
      category_en: nameEn,
      category_ru: nameRu,
      image: !uploadDatas.data
        ? FilterData.map((elem) => elem.image)[0]
        : uploadDatas.data,
    };
    await dispatch(CategoryPut({ id: id, body }));
    dispatch(CategoriesGet());
    HandleClose();
  };

  const antIcon = (
    <LoadingOutlined
      style={{
        fontSize: 24,
        color: "#000",
      }}
      spin
    />
  );
  return (
    <DrawerCommon
      title={"Изменить категорию"}
      open={Open}
      onClose={HandleClose}
    >
      <>
        <Wrapper onSubmit={HandleSubmit}>
          <div>
            <div className="input_wrap">
              {DataCategory.map((elem) =>
                elem.id == id ? (
                  <>
                    <Row className="row">
                      <Col className="col" lg={4}>
                        <Row className="row">
                          <Col className="col_upload" lg={6}>
                            {uploadDatas.Loading == true ? (
                              <div className="spinss">
                                <Spin indicator={antIcon} />
                              </div>
                            ) : uploadDatas.Success == true ? (
                              <Image
                                width="100%"
                                height="100%"
                                style={{
                                  aspectRatio: "1 / 1",
                                  borderRadius: "20px",
                                  zIndex: "99999999",
                                  objectFit: "cover",
                                }}
                                src={uploadDatas.data}
                              />
                            ) : (
                              <Image
                                width="100%"
                                height="100%"
                                style={{
                                  aspectRatio: "1 / 1",
                                  borderRadius: "20px",
                                  zIndex: "99999999",
                                  objectFit: "cover",
                                }}
                                src={elem.image}
                              />
                            )}
                          </Col>
                          <Col className="col_upload" lg={6}>
                            {uploadDatas.Loading == true ? (
                              <div className="spins">
                                <Spin indicator={antIcon} />
                              </div>
                            ) : (
                              <>
                                <input
                                  type="file"
                                  id="file"
                                  onChange={HandleChange}
                                />
                                <label for="file" class="custom-file-upload">
                                  <span className="span-download">
                                    <ion-icon name="cloud-download-outline"></ion-icon>
                                    <span>Загрузить фото</span>
                                  </span>
                                </label>
                              </>
                            )}
                          </Col>
                        </Row>
                      </Col>
                      <Col className="col" lg={8}>
                        <h2>Название категори</h2>
                        <Row className="row">
                          <Col className="col" lg={6}>
                            <h4>На русском</h4>
                            <InputCommon
                              defaultValue={elem.category_ru}
                              onChange={(e) => setNameRu(e.currentTarget.value)}
                            />
                          </Col>
                          <Col className="col" lg={6}>
                            <h4>На английском</h4>
                            <InputCommon
                              defaultValue={elem.category_en}
                              onChange={(e) => setNameEn(e.currentTarget.value)}
                            />
                          </Col>
                          <Col className="col" lg={6}>
                            <h4>На узбекском</h4>
                            <InputCommon
                              defaultValue={elem.category_uz}
                              onChange={(e) => setNameUz(e.currentTarget.value)}
                            />
                          </Col>
                        </Row>
                      </Col>
                    </Row>
                  </>
                ) : null
              )}
            </div>
          </div>
          <CommonBtn
            type={"submit"}
            style={{
              padding: "12px 40px",
              border: "2px solid #fff",
            }}
          >
            Изменить
          </CommonBtn>
        </Wrapper>
      </>
    </DrawerCommon>
  );
}

export default PutCategory;
